import './menu.css';
import { Link } from 'react-router-dom';

function EditorMenu(props) {

  // const []

  // const switchEditState = (e) => {
  //   props.changeEditState();
  //   if(props.editState === 'edit') {

  //   }
  // };

  return (
    <div className="editor menu">
      <Link
        to={{
            pathname: '/',
        }}
      >
        <button id="home" className="menu-item" title="">
        </button>
      </Link>
      <button id={props.editState} className="menu-item" onClick={props.changeEditState} title="">
      </button>
      <button id="save" className="menu-item" onClick={props.saveEditorChange} title="">
      </button>
      <button id="preview" className="menu-item" title="">
      </button>
      {/* <button id="lunch" className='menu-item'></button> */}
        
    </div>
  );
}

export default EditorMenu;